.head {
  padding: 20px;
  color: #fff;
}
.logo h1 {
  font-size: 35px;
  font-weight: 900;
}

.head .icon {
  margin-left: 10px;
}
/* -------------Main Header and navbar--------------- */
header {
  background-color: rgba(255, 255, 255, 0.2);
  margin: 0 30px;
}
header ul {
  padding: 30px 20px;
  list-style: none;
}
header li {
  margin-right: 40px;
}
header ul li a {
  color: #fff;
  font-weight: 900;
  text-decoration: none;
  transition: 0.3 ease;
}
header ul li a:hover {
  color: #1eb2a6;
}
.start {
  background-color: #1eb2a6;
  padding: 30px 70px;
  color: #fff;
  clip-path: polygon(10% 0, 100% 0, 100% 100%, 0 100%);
}
.toggle {
  display: none;
}
@media screen and (max-width: 768px) {
  .start {
    clip-path: none;
  }
  header {
    margin: 0;
    background-color: #1eb2a6;
    position: relative;
  }
  header ul.flexSB {
    display: none;
  }
  header ul li {
    margin-bottom: 20px;
  }
  .toggle {
    display: block;
    cursor: pointer;
    position: absolute;
    right: 50px;
    top: -25px;
    background: none;
    color: #fff;
    font-size: 30px;
  }
  .mobile-nav {
    position: absolute;
    top: 7vh;
    width: 100%;
    height: calc(92vh - 7vh);
    left: 0;
    background-color: #1eb2a6;
  }
}
